*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color:#d2dbdd;
}

div{
  width:100%;
  height:100vh;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}
button{
  padding:15px 20px;
  background-color:#9b59b6;
  color:white;
  border:2px solid#ecf0f1;
  outline:none;
  border-radius:5px;
  text-transform:uppercase;
  cursor:pointer;
}